import {
    debounce,
    isPoneAvailable
} from '@/utils'
export default {
    data() {
        return {
            errorUserName: false,
            errorUserNameMsg: '',
            errorPassword: false,
            errorPasswordMsg: '',
            errorPassword1: false,
            errorPassword1Msg: '',
            errorVerifyCode: false,
            errorVerifyCodeMsg: ''
        }
    },
    watch: {
        userName() {
            this.debounceUserName()
        },
        verifyCode() {
            this.debounceVerifyCode()
        },
        password() {
            this.debouncePassword()
        },
        password1(){
            this.debouncePassword1()
        }
    },
    methods: {
        validate() {
            this.debounceUserName = debounce(this.validateUserName)
            this.debounceVerifyCode = debounce(this.validateVerifyCode)
            this.debouncePassword = debounce(this.validatePassword)
            this.debouncePassword1 = debounce(this.validatePassword1)
        },
        validatePassword() {
            if (this.password.length >= 6 && this.password.length <= 20) {
                this.errorPassword = false
                this.errorPasswordMsg = ''
            } else {
                this.errorPassword = true
            }
        },
        validatePassword1() {
            if (this.password1.length >= 6 || this.password.length1 <= 20) {
                this.errorPassword1 = false
                this.errorPassword1Msg = ''
                this.errorPasswordMsg = ''
            } else {
                this.errorPassword1 = true
            }
        },
        validateVerifyCode() {
            if (this.verifyCode.length === 6) {
                this.errorVerifyCode = false
                this.errorVerifyCodeMsg = ''
            } else {
                this.errorVerifyCode = true
            }
        },
        validateUserName() {
            if (isPoneAvailable(this.userName)) {
                this.errorUserName = false
                this.errorUserNameMsg = ''
                this.disabled = false
            } else {
                this.disabled = true
                this.errorUserName = true
            }
        }
    }

}
