import md5 from 'js-md5'
export default {
  methods: {
    loginLM(username, password) {
      this.$store
        .dispatch('login', {
          UserName: username,
          Password: md5(password),
          role: localStorage.getItem('role')
        })
        .then(() => {
          this.$router.replace({
            path: this.redirect || '/'
          })
        })
        .catch(() => {})
    }
  }
}
