<template>
  <div class="register">
    <van-cell-group>
      <field
        v-model="userName"
        center
        clearable
        type="tel"
        maxLength="11"
        label="账号"
        placeholder="请输入账号"
        :error="errorUserName"
        :error-message="errorUserNameMsg"
      >
        <van-button
          slot="button"
          size="small"
          type="primary"
          :disabled="disabled"
          @click="sendVerifyCode"
        >发送验证码</van-button>
      </field>
      <field
        v-model="verifyCode"
        center
        clearable
        type="number"
        maxLength="6"
        label="验证码"
        placeholder="请输入短信验证码"
        :error="errorVerifyCode"
        :error-message="errorVerifyCodeMsg"
      />
      <field
        v-model="password"
        center
        clearable
        label="密码"
        placeholder="请输入密码"
        :error="errorPassword"
        :error-message="errorPasswordMsg"
      />
      <field
        v-model="password1"
        center
        clearable
        label="确认密码"
        placeholder="请再次输入确认密码"
        :error="errorPassword1"
        :error-message="errorPassword1Msg"
      />
    </van-cell-group>
    <div class="register-bt">
      <van-button
        size="large"
        type="primary"
        @click="register"
      >注册</van-button>
    </div>
  </div>
</template>

<script>
import { Field } from "vant";
import mixins from "./mixins";
export default {
  components: { Field },
  mixins: [mixins],
  mounted() {
    this.validate();
  }
};
</script>

<style lang="less" scoped>
.register-bt {
  padding: 40px;
  .van-button--large {
    width: 100%;
    height: 40px;
    line-height: 38px;
    border-radius: 10px;
  }
}
</style>
