export default {
  methods: {
    sendVerifyCode() {
      if (this.userName === '' ||  this.errorUserName) {
        this.errorUserNameMsg = '请输入账号'
      } else {
        this.moblieCheck()
          .then(() => {
            this.getVerifyCode()
          })
          .catch(() => {})
      }
    },
    moblieCheck() {
      return new Promise((resolve, reject) => {
        this.$store
          .dispatch('mobileCheck', this.userName)
          .then(() => {
            resolve()
          })
          .catch(error => {
            reject(error)
          })
      })
    },
    getVerifyCode() {
      this.$store
        .dispatch('getVerifyCode', this.userName)
        .then(() => {
          this.authTime()
          this.$toast('短信验证码已发送')
        })
        .catch(() => {

        })
    },
    authTime() {
      // 设置倒计时秒
      let authTime = 60
      this.disabled = true
      this.codeText = `（${authTime}）后重发`
      this.authTimetimer = setInterval(() => {
        authTime--
        this.codeText = `（${authTime}）后重发`
        if (authTime <= 0) {
          this.disabled = false
          this.codeText = '再次发送'
          clearInterval(this.authTimetimer)
        }
      }, 1000)
    }
  },
}
