import md5 from 'js-md5'
import login from '@/mixins/login'
export default {
    mixins: [login],
    methods: {
        register() {
            if (this.userName === '' || this.errorUserName) {
                this.errorUserNameMsg = '请输入手机号码'
            } else if (this.verifyCode === '' || this.errorVerifyCode) {
                this.errorVerifyCodeMsg = '请输入短信验证码'
            } else if (this.password === '' || this.errorPassword) {
                this.errorPasswordMsg = '请输入密码'
            } else if (this.password1 === '' || this.errorPassword1) {
                this.errorPassword1Msg = '请再次输入密码'
            } else if (this.password !== this.password1) {
                this.errorPasswordMsg = this.errorPassword1Msg = '请确认密码输入正确'
            } else {
                this.verifyCodes()
                    .then(() => {
                        this.registerLM()
                    })
                    .catch(() => {})
            }
        },
        registerLM() {
            this.$store
                .dispatch('regedit', {
                    UserName: this.userName,
                    Password: md5(this.password),
                    RefererAccount: this.refererAccount,
                    role: this.role
                })
                .then(() => {
                    this.loginLM(this.userName, this.password)
                })
                .catch(() => {})
        },
        verifyCodes() {
            return new Promise((resolve, reject) => {
                this.$store
                    .dispatch('verifyCode', {
                        Mobile: this.userName,
                        VerifyCode: this.verifyCode
                    })
                    .then(() => {
                        resolve()
                    })
                    .catch(err => {
                        reject(err)
                    })
            })
        }
    },
}
